<template>
  <div>
    <v-flex>
      <v-file-input
        v-if="!item.file_path"
        v-bind="{ ...$attrs, ...commonAttrs }"
        v-on="$listeners"
        @change="(file) => { handleFileUpload(file) }"
        >
        <template
          v-for="(_, scopedSlotName) in $scopedSlots"
          #[scopedSlotName]="slotData"
        >
          <slot :name="scopedSlotName" v-bind="slotData" />
        </template>
        <template v-for="(_, slotName) in $slots" #[slotName]>
          <slot :name="slotName" />
        </template>
      </v-file-input>
      <v-chip
        v-if="item.file_path"
        class="ma-2"
        @click="openInNewTab(item.file_path)"
        close
        color="green darken-2"
        text-color="white"
        style="margin: 5px"
        @click:close="removeFile"
        >{{item.file_name.substring(0, 50)}}
      </v-chip>
    </v-flex>
  </div>
</template>
<script>
import FileExchange from '../../services/FileExchange'
import { b64toBlobUrl } from '../../services/utilities'
export default {
  inheritAttrs: false,
  props: {
    item: { default: () => { return { file_name: '', file_path: '' } } }
  },
  data () {
    return {}
  },
  methods: {
    async handleFileUpload (file) {
      if (file !== null) {
        const result = await FileExchange.uploadToStorage(file)
        this.item.file_size = file.size
        this.item.file_type = file.type
        this.item.file_name = file.name
        this.item.file_path = result.data.path
      } else {
        this.item.file_name = ''
        this.item.file_path = ''
        this.item.file_size = ''
        this.item.file_type = ''
      }
      this.$emit('file-changed', this.item)
    },
    async openInNewTab (url) {
      try {
        const result = await FileExchange.getFileFromStorage(url)
        const fileData = result.data
        const urlBlob = b64toBlobUrl(fileData, 'image/jpeg', '')
        var win = window.open(urlBlob, '_blank')
        win.focus()
      } catch (error) {
        console.log(error)
      }
    },
    removeFile () {
      this.item.file_name = ''
      this.item.file_path = ''
      this.item.file_size = ''
      this.item.file_type = ''
      this.$emit('file-changed', this.item)
    }
  },
  computed: {
    commonAttrs () {
      return {
        persistentHint: true,
        dense: true,
        hideDetails: false
      }
    }
  }
}
</script>
