<template>
  <div id="adjuntos">
    <v-card
      class="mx-auto"
      dense
      v-for="item in dataAdjuntos" :key="item.tipo"
    >
        <v-card
          v-if="(item.tipo == 1) || (item.tipo == 2 && cargardefinitivo == 1) "
        >
            <v-card-title>{{ `${item.tipo == 1 ? 'Preliminar' : 'Definitivo'}` }}</v-card-title>
            <v-card-text>
              <v-btn text>
                  <AddFilesToStorage
                    :item="item"
                    placeholder="Cargar Archivo"
                    dense
                    hide-details
                    prepend-icon
                    @remove-file="fileReset++"
                    :key="fileReset"
                    @file-changed="fileReset++"
                  />
                </v-btn>
                <!--<v-chip
                    class="ma-2"
                    @click="openInNewTab(item.file_path, item.file_type)"
                    color="green darken-2"
                    text-color="white"
                    style="margin: 5px"
                >
                    {{item.file_name.substring(0, 50)}}
                </v-chip>-->
                <v-autocomplete
                    v-model="item.estado"
                    :items="estados"
                    label="Validación"
                    item-text="texto"
                    item-value="id"
                    hide-details
                    :cache-items="false"
                >
                </v-autocomplete>
                <br>
                <v-textarea
                    v-model="item.observacion"
                    name="input-7-1"
                    filled
                    label="Observación"
                    rows="1"
                ></v-textarea>
            </v-card-text>
            <v-card-actions>
            <v-btn
                class="mx-2"
                fab
                dark
                color="primary"
                @click="guardarEstadoMinuta(item)"
            >
                <v-icon dark>
                    save
                </v-icon>
            </v-btn>
            </v-card-actions>
        </v-card>
        <br>
    </v-card>
  </div>
</template>
<script>
import AddFilesToStorage from '@/components/shared/AddFilesToStorage'
import { adminApp } from '@/services/api'
import FileExchange from '../../../services/FileExchange'
import { b64toBlobUrl } from '../../../services/utilities'
export default {
  props: ['idpropconv'],
  components: { AddFilesToStorage },
  data () {
    return {
      dataAdjuntos: [],
      resetComponent: 0,
      fileReset: 0,
      cargardefinitivo: 0,
      estados: [
        { id: 'A', texto: 'APROBADO' },
        { id: 'R', texto: 'RECHAZADO' }
      ]
    }
  },
  async mounted () {
    this.getData()
  },
  methods: {
    async getData () {
      this.dataAdjuntos = []
      const { data } = await adminApp.buscarMinutaContrato({
        idpropconv: this.idpropconv
      })
      this.dataAdjuntos = data.archivos
      if (this.dataAdjuntos.length > 0) {
        const element = this.dataAdjuntos[0]
        if (element.tipo === 1 && element.estado === 'A') {
          this.cargardefinitivo = 1
        }
      }
    },
    async openInNewTab (url, type) {
      try {
        const result = await FileExchange.getFileFromStorage(url)
        const fileData = result.data
        const urlBlob = b64toBlobUrl(fileData, type, '')
        var win = window.open(urlBlob, '_blank')
        win.focus()
      } catch (error) {
        console.log(error)
      }
    },
    async guardarEstadoMinuta (item) {
      if (item.estado === '' || item.observacion === '') {
        this.$toasted.error('Debe ingresar el estado y  la observación', { icon: 'error', duration: 5000 })
        return
      }
      const { data } = await adminApp.voboMinutaContrato({
        idpropconv: this.idpropconv,
        tipo: item.tipo,
        estado: item.estado,
        observacion: item.observacion
      })
      if (data) {
        this.$toasted.success('Se actualizó el estado de la minuta con éxito', { icon: 'done', duration: 5000 })
        this.getData()
      } else {
        this.$toasted.error('Error desconocido', { icon: 'error', duration: 5000 })
      }
    }
  },
  computed: {
    adjuntos: {
      get () {
        return this.dataAdjuntos
      },
      set (val) {
        this.adjuntos = val
      }
    }
  },
  watch: {
    idpropconv (val) {
      this.getData()
    }
  }
}
</script>
