  .anexosClass {
    min-width: 300px;
  }
  #adjuntos .v-label {
    font-size: .70rem;
  }
  #adjuntos .v-label .inFiles{
    font-size: .95rem;
  }
  .ColorVBTrue{
    color:blue;
    font-weight: bold;
  }
  .ColorVBFalse{
    color:red;
    font-weight: bold;
  }
</style>
